@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../../assets/fonts/Roboto-Regular.woff2") format("woff2"), url("../../assets/fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Prompt";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../../assets/fonts/Prompt-Light.woff2") format("woff2"), url("../../assets/fonts/Prompt-Light.woff") format("woff");
}
@font-face {
  font-family: "Prompt";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../../assets/fonts/Prompt-Medium.woff2") format("woff2"), url("../../assets/fonts/Prompt-Medium.woff") format("woff");
}
@font-face {
  font-family: "Prompt";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../../assets/fonts/Prompt-SemiBold.woff2") format("woff2"), url("../../assets/fonts/Prompt-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  min-width: 320px;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #FFFFFF;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #444444;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  border: 1px solid transparent;
  vertical-align: middle;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.promo-block {
  padding-bottom: 90px;
}

.promo-block__wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .promo-block {
    padding: 90px 0;
  }
  .promo-block__wrapper {
    width: 94%;
    margin: 0 auto;
    flex-direction: row;
    padding: 0;
  }
}
.building {
  width: 100%;
}

.building__wrapper {
  display: flex;
  flex-direction: column;
}

.building__info-block {
  position: relative;
  padding-top: 80px;
}

.building__info-block::before {
  content: "";
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: 60px 60px;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.building__info-block--prom::before {
  background-image: url("../assets/icons/prom-icon.svg");
}

.building__info-block--energo::before {
  background-image: url("../assets/icons/energo-icon.svg");
}

.building__info-block--civil::before {
  background-image: url("../assets/icons/civil-icon.svg");
}

.building__title {
  font-family: "Prompt", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #262626;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 32px;
}

.building__text {
  display: none;
}

.building__image-block {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.building__image {
  width: 100%;
  display: block;
}

.building__overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  z-index: 999;
}

.building__button {
  position: absolute;
  padding: 10px 20px;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: transparent;
  border: 1px solid #FFFFFF;
  z-index: 1000;
}

.building__button span {
  font-family: "Prompt", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .building {
    width: 33.333%;
    transition: all 0.5s ease;
  }
  .building:hover {
    cursor: pointer;
    background-color: rgba(21, 114, 199, 0.07);
    transition: all 0.5s ease;
  }
  .building--prom .building__wrapper,
  .building--civil .building__wrapper {
    flex-direction: column-reverse;
  }
  .building__info-block {
    width: 100%;
    min-height: 400px;
    padding-top: 190px;
    perspective: 300px;
  }
  .building__info-block::before {
    top: 90px;
    width: 100px;
    height: 100px;
    background-size: 100px 100px;
  }
  .building__title {
    font-size: 22px;
    line-height: 150%;
  }
  .building__title--prom {
    margin-top: 13px;
    margin-bottom: 10px;
  }
  .building__title--civil {
    margin-top: 15px;
    margin-bottom: 10px;
  }
  .building__title--energo {
    margin-top: 25px;
    margin-bottom: 10px;
  }
  .building__text {
    width: 60%;
    margin: 0 auto;
    display: block;
    font-family: "Roboto", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
  }
  .building__image {
    height: 25rem;
    object-fit: cover;
    transition: all 0.5s ease;
  }
  .building:hover .building__image {
    transform: scale(1.2);
  }
  .building__button {
    padding: 10px 34px;
    top: 100%;
    transform: translateX(-50%) translateY(100%);
    transition: all 0.5s ease;
    opacity: 0;
  }
  .building__overlay {
    transition: all 0.5s ease;
    opacity: 0;
  }
  .building:hover .building__button {
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    transition: all 0.5s ease;
    opacity: 1;
    cursor: pointer;
  }
  .building:hover .building__overlay {
    transition: all 0.5s ease;
    opacity: 1;
  }
  .building:hover .building__info-block::before {
    filter: saturate(0);
    transform: translateX(-50%) rotateY(360deg);
    transition: all 0.5s ease;
  }
}
.gallery {
  width: 100%;
  padding-bottom: 78px;
}

.gallery__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .gallery__wrapper {
    width: 86%;
    margin: 0 auto;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;
  }
}
.photo-block {
  width: 100%;
  position: relative;
  margin-bottom: 30px;
}

.photo-block--slider {
  width: 100%;
}

.photo-block__image-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.photo-block__image {
  width: 100%;
  display: block;
}

.photo-block__info {
  width: 90%;
  padding: 16px 19px 16px 40px;
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  border: 1px solid #d3d2d2;
}

.photo-block__title {
  margin: 0;
  font-family: "Prompt", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  color: #FFFFFF;
  font-size: 16px;
  line-height: 120%;
  text-align: left;
  text-shadow: 0px 0px 8px #646262;
}

@media (min-width: 768px) {
  .photo-block {
    width: 49%;
    margin-bottom: 56px;
    cursor: pointer;
  }
  .photo-block--slider {
    width: 100%;
  }
  .photo-block__info {
    width: 50%;
    padding: 22px 20px 22px 30px;
    left: 4%;
    bottom: -22px;
    transform: translateX(0%);
    transition: all 0.5s ease;
  }
  .photo-block:hover .photo-block__info {
    left: 97%;
    transform: translateX(-100%);
  }
  .photo-block__title {
    font-size: 22px;
  }
  .photo-block__image {
    transition: all 0.5s ease;
  }
  .photo-block:hover .photo-block__image {
    transform: scale(1.2);
  }
}
.about-us {
  width: 100%;
  padding-bottom: 50px;
  background-color: rgb(219, 219, 219);
}

.about-us__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.about-us__item {
  width: 100%;
  position: relative;
  padding-top: 80px;
}

.about-us__item::before {
  content: "";
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: 60px 60px;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.about-us__item--energo::before {
  background-image: url("../assets/icons/energo-icon.svg");
}

.about-us__item--civil::before {
  background-image: url("../assets/icons/civil-icon.svg");
}

.about-us__title {
  font-family: "Prompt", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #262626;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 32px;
}

.about-us__text {
  width: 80%;
  margin: 0 auto;
}

.about-us__details {
  position: relative;
  color: #0568C3;
}

.about-us__details::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 20px;
  right: -15px;
  background-image: url("../assets/icons/arrow-right-hover.svg");
  background-repeat: no-repeat;
  background-size: 10px 20px;
}

@media (min-width: 768px) {
  .about-us {
    padding-bottom: 82px;
    background-image: url("../assets/img/white-construction-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left center;
    background-attachment: fixed;
  }
  .about-us__wrapper {
    width: 80%;
    flex-direction: row;
    margin: 0 auto;
  }
  .about-us__item {
    width: 41%;
    text-align: left;
    padding-left: 168px;
  }
  .about-us__item::before {
    left: -32px;
    transform: translateX(100%);
    top: 46%;
    width: 90px;
    height: 90px;
    background-size: 90px 90px;
  }
  .about-us__title {
    margin-top: 18px;
    margin-bottom: 5px;
    font-size: 19px;
    line-height: 25px;
  }
  .about-us__text {
    width: 100%;
    margin: 0;
    margin-bottom: 10px;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
  }
}
.slider {
  width: 100%;
  padding-bottom: 100px;
}

.slider__wrapper {
  width: 100%;
  padding: 0 20px;
}

.slider__title {
  font-family: "Prompt", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #262626;
  text-transform: uppercase;
  font-size: 25px;
  line-height: 40px;
}

.slider__list {
  --cols: 1;
  --margin: 0px;
  --position: 1;
  display: flex;
  overflow: hidden;
  position: relative;
}

.slider__item {
  position: absolute;
  margin: 0 var(--margin);
  width: calc(100% / var(--cols) - 2 * var(--margin));
  flex-shrink: 0;
  transform: translateX(calc(-100% * var(--position)));
  transition-duration: 500ms;
}

.slider__buttons {
  display: none;
}

.slider__toggles {
  display: flex;
}

.slider__toggle {
  width: 15px;
  height: 5px;
  background-color: gray;
}

.slider__toggle--active {
  background-color: blue;
}

@media (min-width: 768px) {
  .slider {
    padding-top: 72px;
  }
  .slider__wrapper {
    width: 92%;
    position: relative;
    padding: 0;
    margin: 0 auto;
  }
  .slider__title {
    margin-bottom: 46px;
    font-size: 26px;
    font-weight: 300;
  }
  .slider__list {
    width: 93%;
    margin: 0 auto;
    --cols: 3;
    overflow: hidden;
  }
  .slider__item {
    padding: 0 7px;
    transform: translateX(0%);
  }
  .slider__buttons {
    display: block;
    position: absolute;
    width: 100%;
    top: 45%;
    display: flex;
    justify-content: space-between;
  }
  .slider__button {
    position: relative;
    width: 27px;
    height: 50px;
    padding: 0;
    font-size: 0;
    background-color: transparent;
    outline: none;
  }
  .slider__button--prev {
    left: 0;
  }
  .slider__button--next {
    right: 0;
  }
  .slider__button--prev::before,
  .slider__button--next::before {
    content: "";
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 27px;
    height: 50px;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 27px 50px;
    transition: all 0.5s ease;
  }
  .slider__button--prev::before {
    background-image: url("../assets/icons/arrow-left.svg");
  }
  .slider__button--next::before {
    background-image: url("../assets/icons/arrow-right.svg");
  }
  .slider__button--prev:hover::before {
    background-image: url("../assets/icons/arrow-left-hover.svg");
  }
  .slider__button--next:hover::before {
    background-image: url("../assets/icons/arrow-right-hover.svg");
  }
}